import { Component, OnInit } from '@angular/core';
import { ApiMargaritaService } from '../api-margarita.service';

@Component({
  selector: 'app-margarita',
  templateUrl: './margarita.component.html',
  styleUrls: ['./margarita.component.css']
})
export class MargaritaComponent implements OnInit {

	margarita :any[] = [];
  constructor(private _api:ApiMargaritaService) { }

  	ngOnInit(): void {
  	let ob = this._api.getAllMargarita();
  	ob.subscribe((res:any)=>{
  	this.margarita = res.drinks;
  	});
  }

}
