import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiHabitatService {

	apiUrl = "https://apiv3.iucnredlist.org/api/v3/habitats/species/name/Loxodonta%20africana?token=9bb4facb6d23f48efbf424bb05c0c1ef1cf6f468393bc745d42179ac4aca5fee";
  	constructor(private _http: HttpClient) { }

  	getAllHabitat(){
  		return this._http.get(this.apiUrl);
  	}
}
